import * as axios from 'axios';
import { ADD_OR_DEL_ITEM_IN_BASKET, GET_CARDS_FAILED, GET_CARDS_REQUEST, GET_CARDS_SUCCESS } from './actionTypes';

export const addItemInBasket = (id) => {
    return (dispatch) => {
        let basketCopy = JSON.parse(localStorage.getItem('basket'));
        basketCopy !== null ? basketCopy.push(id) : basketCopy = [id];
        localStorage.setItem('basket', JSON.stringify(basketCopy));
        basketCopy = JSON.parse(localStorage.getItem('basket'));

        dispatch({
            type: ADD_OR_DEL_ITEM_IN_BASKET,
            basket: basketCopy
        });
    }
}

export const delItemOfBasket = (id) => {
    return (dispatch) => {
        let basketCopy = JSON.parse(localStorage.getItem('basket'));
        const index = basketCopy.indexOf(id);
        basketCopy.splice(index, 1);
        localStorage.setItem('basket', JSON.stringify(basketCopy));
        basketCopy = JSON.parse(localStorage.getItem('basket'));

        dispatch({
            type: ADD_OR_DEL_ITEM_IN_BASKET,
            basket: basketCopy
         });
    }
}

export const setCards = () => {
    return (dispatch) => {
        
        dispatch({
            type: GET_CARDS_REQUEST
        });

        axios.get('https://gist.githubusercontent.com/TenTax/feecd89f8752ec781b52a4ad328ab45e/raw/8a46d89d7a60631461ccbac7fe34431a9e65e688/guitarShop.json')
        .then(res => {
            dispatch({
                type: GET_CARDS_SUCCESS,
                cards: res.data,
            })
        })
        .catch(error => {
            dispatch({
                type: GET_CARDS_FAILED
            })
        });
    }
}