import React from 'react';
import { connect } from 'react-redux';
import CardPlace from './CardPlace';
import { setCards } from '../../redux/actions';

class CardPlaceContainer extends React.Component {
    componentDidMount() {
        this.props.setCards()
    }
    render() {
        return <CardPlace cards={ this.props.cards } isFetching={ this.props.isFetching } failed={ this.props.failed } />;
    }
}

const mapStateToProps = (state) => {
    return { 
        cards: state.cardsPlace.cards,
        isFetching: state.cardsPlace.isFetching,
        failed: state.cardsPlace.failed
    };
}

const mapDispatchToProps = {
    setCards
}

export default connect(mapStateToProps, mapDispatchToProps)(CardPlaceContainer);