import { ADD_OR_DEL_ITEM_IN_BASKET, GET_CARDS_REQUEST, GET_CARDS_SUCCESS, GET_CARDS_FAILED } from '../actionTypes';

const initialState = {
    cards: [],
    isFetching: false,
    failed: false,
    basket: Boolean(localStorage.getItem('basket')) ? JSON.parse(localStorage.getItem('basket')) : []
}

const cardsPlaceReducer = (state = initialState, action) => {
    switch(action.type) {
        case GET_CARDS_REQUEST:
            return {
                ...state,
                isFetching: true
            }

        case GET_CARDS_SUCCESS:
            return {
                ...state,
                cards: action.cards,
                isFetching: false
            }

        case GET_CARDS_FAILED: {
            return {
                ...state,
                failed: true
            }
        }

        case ADD_OR_DEL_ITEM_IN_BASKET: {
            return {
                ...state,
                basket: action.basket
            }
        }

        default:
            return state;
    }
}

export default cardsPlaceReducer;